/* Selectize
----------------------------------------------------------------------*/
.lw-selectize {
    .lw-error-message {
        margin-top: -6px;
    }

    .selectize-input {
        // border-radius: 0;
        height: calc(2.25rem + 2px);

        .selectize-input {
            .item {
                margin-top: 2px;
            }
        }
    }
}

.selectize-control.lw-search-product::before {
    -moz-transition: opacity 0.2s;
    -webkit-transition: opacity 0.2s;
    transition: opacity 0.2s;
    content: ' ';
    z-index: 2;
    position: absolute;
    display: block;
    top: 12px;
    right: 34px;
    width: 16px;
    height: 16px;
    background: url(./../../static-assets/imgs/spinner.gif);
    background-size: 16px 16px;
    opacity: 0;
}

.selectize-control.lw-search-product.loading::before {
    opacity: 0.4;
}

.form-control.selectize-control {
    height: 34px;
}

.lw-addon-selectize .selectize-input {
    border-radius: 0px;
    -webkit-border-top-left-radius: 4px;
    -webkit-border-bottom-left-radius: 4px;
    -moz-border-radius-topleft: 4px;
    -moz-border-radius-bottomleft: 4px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
}

.lw-addon-btn-selectize {
    height: 38px;
    margin-top: 0px !important;
}

/* ngdialog
----------------------------------------------------------------------*/
.ngdialog.ngdialog-theme-default {
    padding: 0;
    @include lwTransition();

    ~.ngdialog {
        margin-left: 40px;

        ~.ngdialog {
            margin-left: 80px;
        }


        .ngdialog-overlay {
            background: rgba(0, 0, 0, 0.1);
        }
    }

    .modal-header {
        top: 0;
        z-index: 2;
        background: #fff;
        color: #1a1a1a;
        width: 100%;
        border-radius: 0;
        box-shadow: 0 0.01rem 1rem rgba(0, 0, 0, 0.15);
        height: 56px;

        h3,
        .modal-title {
            line-height: 1;
            font-size: 1.4rem;
        }
    }

    div.ngdialog-content {
        z-index: 10;
        display: block;
        background-color: white;
        margin: 0 auto;
        width: 70%;
        padding: 0;
        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
        /*-webkit-animation: ngdialog-flyin .2s;
        animation: ngdialog-flyin 0.2s;*/

        @media (max-width: 767px) {
            width: 90%;
        }

        @media (min-width: 1200px) {
            width: 70%;
        }

        @media (min-width: 1400px) {
            width: 60%;
        }
    }

    .ngdialog-overlay {
        z-index: 1;
        margin-right: 16px;
    }

    .ngdialog-close {
        background: transparent;
        border: none;
        z-index: 2;
        right: 6px;
        top: 6px;

        &:before,
        &:hover,
        &:active:before,
        &:hover:before {
            color: #1a1a1a;
        }

        &:before {
            position: relative;
        }
    }
}

